@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,600,700&display=swap);
/*
  Styles for the Header component
*/
.header {
  height: 63px;
  width: 100%;

  z-index: 900;
  border-bottom: 1px solid rgba(200, 200, 200, 0.3);

  display: flex;
  align-items: center;
}

.logo-wrapper {
  width: 256px;
  display: flex;
  align-items: center;
}

.hamburger-wrapper {
  width: 50px;
  height: 50px;
  margin-left: 13px;
  border-radius: 100px;
  cursor: pointer;
}

.hamburger-wrapper:hover {
  background-color: rgba(200, 200, 200, 0.1);
}

.hamburger {
  width: 30px;
  height: 30px;
  display: block;
  margin: auto;
  margin-top: 10px;
}

.gmail-logo {
  margin-left: 5px;
  height: 40px;
  cursor: pointer;
}

.search-bar {
  width: 700px;
  min-width: 300px;
  height: 47px;
  display: flex;
  align-items: center;

  background-color: rgba(200, 200, 200, 0.3);
  border-radius: 8px;
}

.search-icon {
  fill: #eeeeee;
  margin: 0 10px;
}

.search-input {
  height: 48px;
  background-color: transparent;
  border: none;
  color: #eeeeee;
}
/*
  Styles for the Sidebar component
*/
.sidebar {
  height: 100%;
  width: 256px;

  flex-shrink: 0;
}

.compose-button {
  width: 135px;
  height: 50px;
  margin: 20px 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #faf9f4;
  border-radius: 30px;
  color: #424242;
  font-weight: 600;
}

.filter-button {
  width: 200px;
  height: 30px;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #faf9f4;
  border-radius: 0 30px 30px 0;
  cursor: pointer;
}

.filter-button:hover {
  background-color: rgba(200, 200, 200, 0.2)
}

.filter-active {
  background-color: rgba(200, 200, 200, 0.4) !important;
}
/*
  Contains styles for the following components:
  MailViewer
  MailToolbar
  MailList
  SingleEmailView
*/
.mail-viewer {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

/*
  Mail List Styles
*/
.mail-list {
  flex-grow: 1;
  background-color: rgba(250, 249, 244, 0.5);
}

.mail-list-container {
  overflow-y: scroll;
  flex-grow: 1;
  background-color: rgba(250, 249, 244, 0.5);
}

.category-container {
  display: flex;
  background-color: #faf9f4;
}

.category {
  height: 60px;
  width: 200px;
  padding-left: 50px;
  display: flex;
  align-items: center;
  font-weight: 600;
  cursor: pointer;
  box-sizing: border-box;
}

.category:hover {
  background-color: #d9d9d9;
}

.all-tag {
  color: #d73122;
  border-bottom: 2px solid #d73122;
}

.travel-tag {
  color: #1374d8;
  border-bottom: 2px solid #1374d8;
}

.work-tag {
  color: #18813a;
  border-bottom: 2px solid #18813a;
}

/*
  Mail Toolbar Styles
*/
.mail-toolbar {
  height: 48px;
  width: 100%;
  display: flex;
  flex-shrink: 0;
  align-items: center;
}

.active-toolbar {
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 5px;
}

.toolbar-icon-wrapper {
  padding: 5px;
  margin-right: 10px;
  border-radius: 100px;
  cursor: pointer;
}

.select-all {
  width: 15px;
  height: 15px;
  border: 2px solid #e1e1e1;
  border-radius: 4px;
}

.selected {
  background-color: #424242;
}

.toolbar-icon-wrapper:hover {
  background-color: rgba(250, 249, 244, 0.3);
}

.toolbar-icon {
  fill: #d1d1d1;
}

/*
  Single Email View Styles
*/
.single-email-view {
  flex-grow: 1;
  background-color: rgba(250, 249, 244, 0.5);
  overflow-y: scroll;
}

.email-container {
  padding: 20px 70px;
  background-color: #faf9f4;
}

.subject-full {
  font-weight: 500;
  font-size: 28px;
}

.email-details {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.sender-full {
  font-size: 14px;
}

.to {
  color: #a6a6a6;
  font-size: 14px;
}

.date-and-actions {
  display: flex;
  align-items: center;
}

.icon-dark {
  fill: #a1a1a1;
}

.icon {
  margin: 0px 10px;
}

.date-full {
  color: #a6a6a6;
  margin-right: 10px;
}

#email-body p {
  margin-top: 20px;
  font-size: 14px;
}

.button-container {
  display: flex;
  margin-top: 10px;
}

.button {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px 10px;
  margin-right: 20px;
  color: #a1a1a1;
  border: 1px solid #d6d6d6;
  border-radius: 5px;
}

@media only screen and (max-width: 960px) {
  .email-container {
    padding: 20px 30px;
    background-color: #faf9f4;
  }

  .date-and-actions .icon-wrapper {
    display: none;
  }

  .email-details {
    display: inline;
  }
}
/*
  Styles for the EmailListItem component
*/

.email {
  height: 50px;

  display: flex;
  align-items: center;

  border-top: 1px solid rgba(200, 200, 200, 0.5);
  box-sizing: border-box;
  cursor: pointer;

  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
}

.email-mobile {
  display: none;
}

.icon-wrapper {
  flex-shrink: 0;
}

.email-text {
  display: flex;
}

.read {
  background-color: rgba(200, 200, 200, 0.7);
}

.unread {
  background-color: #faf9f4;
}

.checkbox {
  width: 15px;
  height: 15px;
  border: 2px solid #e1e1e1;
  border-radius: 4px;
}

.checked {
  background-color: #424242;
}

.email:hover {
  border-bottom: 2px solid #727272;
  border-top: 1px solid #727272;
}

.email:hover .star-inactive {
  fill: #c1c1c1;
}

.email:hover .checkbox {
  border: 2px solid #c1c1c1;
}

.email-sender {
  width: 200px;
  margin: 0 10px;
}

.email-subject {
  margin-right: 10px;
}

.email-excerpt {
  max-height: 100%;
  min-width: 0;

  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 1;
  overflow: hidden;

  color: #696969;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.email-date {
  margin: 0 20px;
}

.bold {
  font-weight: bold;
}

@media only screen and (max-width: 960px) {
  .email-mobile {
    display: flex;
    height: 100px;
  }
  
  .email-details-mobile {
    width: 100%;
    margin-right: 10px;
  }

  .email-sender {
    width: auto;
    font-size: 12px;
  }

  .email-date {
    width: auto;
    font-size: 12px;
  }


  .mobile-sender-container {
    display: flex;
    justify-content: space-between;
  }

  .mobile-email-snippet {
    width: 90%;
    margin-left: 10px;
  }
}
/*
  Styles for the Dock component
*/
.dock {
  width: 55px;
  height: 100%;
  
  border-left: 1px solid rgba(200, 200, 200, 0.3);

  flex-shrink: 0;
}
/*
  Global Styles
*/
* {
  margin: 0;
  padding: 0;

  overflow: hidden;
  font-family: "Open Sans", sans-serif;
}

.App {
  width: 100vw;
  height: 100vh;

  background-color: #0e0e1e;
}

.app-wrapper {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
}

.main-wrapper {
  flex-grow: 1;
  display: flex;
  width: 100%;
  z-index: 9999;
}

.bg-image {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1;

  background-image: url("https://www.wheretraveler.com/sites/default/files/skyline-night_c-richardcawood-flickr.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  filter: blur(12px);
  -webkit-filter: blur(12px);
}

.icon-wrapper {
  padding: 5px;
  margin: 5px;
  cursor: pointer;
  border-radius: 100px;
}

.icon-wrapper:hover {
  background-color: #3299bb;
}

.star-active {
  fill: #e3b23c;
}

.star-inactive {
  fill: #e1e1e1;
}

@media only screen and (max-width: 960px) {
  .header, .sidebar, .dock, .category-container, .email-desktop {
    display: none;
  }

  .mobile-spacer {
    height: 100px;
  }

  .icon-wrapper:hover {
    background-color: none;
  }
}
